import {NgModule} from '@angular/core';
import { HttpClientModule } from '@angular/common/http';

import {NgbModule} from '@ng-bootstrap/ng-bootstrap';

import { HandleValidationsDirective } from './handle-validations.directive';
import { ValidationErrorMsgComponent } from './validation-error-msg/validation-error-msg.component';

@NgModule({
    declarations : [
        HandleValidationsDirective,
        ValidationErrorMsgComponent
    ],
    imports: [
        NgbModule,
        HttpClientModule
    ],
    exports: [
        HandleValidationsDirective,
        ValidationErrorMsgComponent,
        NgbModule,
        HttpClientModule
    ]
})
export class SharedModule {}