import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { Validators } from '@angular/forms';
import { UserAuthServiceService } from '../user-auth-service.service';
import {Router} from '@angular/router';


@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.less']
})
export class SignupComponent implements OnInit {

  signUpForm;
  signUpFailure: boolean = false;
  constructor(private auth: UserAuthServiceService,
      private router: Router) { 
    this.signUpForm = new FormGroup({
      "id" : new FormControl('', [Validators.required, Validators.email]),
      "password" : new FormControl('', [Validators.required, Validators.minLength(8)]),
      "firstName" : new FormControl('', Validators.required),
      "lastName" : new FormControl('', Validators.required),
      "location" : new FormControl('', Validators.required),
      "contactNo" : new FormControl('', [Validators.required, Validators.minLength(10)])
    });
  }

  ngOnInit(): void {
  }

  signUp(): void {
    if (this.signUpForm.status === 'VALID') {

      this.auth.checkIfAlreadyExists(this.signUpForm.value.id).subscribe((resData) => {
        this.signUpFailure = true;
      },
      (error) => {
        if (error.status === 404) {
          this.auth.createUser(this.signUpForm.value).subscribe((resData) => {
            this.auth.updateLoginStatus(resData);
            this.router.navigate(['/']);
          })
        } else {
          this.signUpFailure = true;
        }
      });
      
    }
  }
}
