import { Directive, ComponentRef, ViewContainerRef, ComponentFactoryResolver } from '@angular/core';
import { NgControl } from '@angular/forms';
import {ValidationErrorMsgComponent} from './validation-error-msg/validation-error-msg.component';



const errorMsg = {
  'required' : 'Please fill this required field',
  'email' : 'Please enter email in correct format',
  'pattern': 'Please enter in correct format'
};


@Directive({
  selector: '[formControlName]'
})
export class HandleValidationsDirective  {

  compRef : ComponentRef<ValidationErrorMsgComponent>;

  constructor(private control : NgControl, private vcr: ViewContainerRef,
    private resolver: ComponentFactoryResolver) { }

  ngDoCheck(): void {
    let control = this.control;
    if(control && control.touched && control.errors) {
      let errorType = Object.keys(control.errors);

      if (errorType[0] !== 'minlength') {
        this.addErrorMsg(errorMsg[errorType[0]]);
      } else {
        this.addErrorMsg('Value should have atleast ' + control.errors.minlength.requiredLength + ' characters');
      }
    } else if (control && !control.errors && this.compRef) {
      this.compRef.instance.msg = null;
    }
  }

  addErrorMsg(errorMsg: string) {

    if (!this.compRef) {
      let compFactory = this.resolver.resolveComponentFactory(ValidationErrorMsgComponent);
      this.compRef = this.vcr.createComponent(compFactory);
    }

    this.compRef.instance.msg = errorMsg;
  }
}

