import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { Validators } from '@angular/forms';
import { UserAuthServiceService } from '../user-auth-service.service';


@Component({
  selector: 'app-view-registration-details',
  templateUrl: './view-registration-details.component.html',
  styleUrls: ['./view-registration-details.component.less']
})
export class ViewRegistrationDetailsComponent implements OnInit {

  updateResgitrationDetailsForm;
  isEdit: boolean = false;
  isUpdated: boolean = false;
  constructor(private auth: UserAuthServiceService) {
    this.updateResgitrationDetailsForm = new FormGroup({
      "id" : new FormControl(auth.activeUserData.id, [Validators.required, Validators.email]),
      "password" : new FormControl(auth.activeUserData.password, [Validators.required, Validators.minLength(8)]),
      "firstName" : new FormControl(auth.activeUserData.firstName, Validators.required),
      "lastName" : new FormControl(auth.activeUserData.lastName, Validators.required),
      "location" : new FormControl(auth.activeUserData.location, Validators.required),
      "contactNo" : new FormControl(auth.activeUserData.contactNo, [Validators.required, Validators.minLength(10)])
    });
   }

  ngOnInit(): void {
  }

  edit(): void {
    this.isEdit = true;
    this.isUpdated = false;
  }

  submit(): void {
    if (this.updateResgitrationDetailsForm.status === 'VALID') {
      this.auth.updateUser(this.updateResgitrationDetailsForm.value).subscribe((resData) => {
        this.auth.updateLoginStatus(resData);
        this.update();
      });
    }
  }

  update(): void {
    this.isUpdated = true;
    this.isEdit = false;
  }

  canDeactivate(): boolean {
    if (this.isEdit && !this.isUpdated) {
      return window.confirm('If you proceed, changes will not be saved.Continue ? ');
    } else {
      return true;
    }
  }
}
