import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { Validators } from '@angular/forms';
import {UserAuthServiceService} from '../user-auth-service.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.less']
})
export class LoginComponent implements OnInit {

  loginForm;
  loginFailure = false;
  constructor(private auth: UserAuthServiceService,
      private router: Router) { 
    this.loginForm = new FormGroup({
      id : new FormControl('', [Validators.required, Validators.email]),
      password : new FormControl('', Validators.required)
    });
  }

  ngOnInit(): void {
  }

  login(): void {
    if (this.loginForm.status === 'VALID') {
      this.auth.checkIfAlreadyExists(this.loginForm.value.id).subscribe((responseData) => {

        if (this.loginForm.value.password === responseData.password) {
          this.auth.updateLoginStatus(responseData);
          this.router.navigate(['/']);
        } else {
          this.loginFailure = true;
        }

      },
      (error) => {
        if (error.status === 404) {
          this.loginFailure = true;
        }
      });
    }
  }
}
