import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from '../login/login.component';
import { SignupComponent } from '../signup/signup.component';
import { AboutComponent } from '../about/about.component';
import { ViewRegistrationDetailsComponent } from '../view-registration-details/view-registration-details.component';
import { AuthenticateGuard } from './authenticate.guard';
import {CanDeactivateGuard} from './deactivate.guard';


const routes: Routes = [
  {
    'path' : 'login',
    'component' : LoginComponent
  },
  {
    'path' : 'signup',
    'component' : SignupComponent
  },
  {
    'path' : 'registrationdetails',
    'component' : ViewRegistrationDetailsComponent,
    'canActivate' : [AuthenticateGuard],
    'canDeactivate' : [CanDeactivateGuard]
  },
  {
    'path' : 'products',
    'loadChildren' : () => import('../products/products.module').then(m => m.ProductsModule)
  },
  {
    'path' : '',
    'redirectTo' : '/products/productsummary',
    'pathMatch' : 'full'
  },
  {
    'path' : '**',
    'redirectTo' : '/products/productsummary'
  }
];


@NgModule({
  declarations: [],
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class RoutingModule { }
