import { Injectable } from '@angular/core';
import {UserDetail} from './user-detail';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserAuthServiceService {

  userDetails = [
    {
      "id": "aaa@aaa.aa",
      "password": "aaaaaaaa",
      "firstName": "aaa",
      "lastName": "lastname",
      "location": "delhi",
      "contactNo": "1111222222"
    }
  ];
  activeUserData = null;

  constructor(private http: HttpClient) { }

  createUser(userDetail):Observable<UserDetail> {

    this.userDetails.push(userDetail);
    return of<UserDetail>(userDetail);
  }

  checkIfAlreadyExists(id: string) {
    var userAccount= [];
    userAccount = this.userDetails.map(val => {
      if (val.id === id) {
          return val;
      }
    });

    if (userAccount[0]) {
      return of(userAccount[0]);
    }

    return of({});
  }

  updateUser(userdetail):Observable<UserDetail> {

    this.userDetails = this.userDetails.map((val,index) => {
      if (val.id === userdetail.id) {
        this.userDetails[index] = userdetail;
      }
      return val;
  });
    return of<UserDetail>(userdetail);
  }

  updateLoginStatus(userData: UserDetail) {
    this.activeUserData = userData;
  }

  logout(): void{
    this.activeUserData = null;
  }
}
